<template>
	<div>
		<div class="flex justify-between items-center mb-8">
			<Breadcrumb
				:back-to="{ name: 'settingCustomerPortal' }"
				:items="breadcrumb"
				page-title="Edit Template Revised Offer" />
		</div>
		<Card>
			<div class="mb-4 w-full md:w-1/2">
				<label class="mb-2">Title</label>
				<Input v-model="data.email_template.name" class="w-full" disabled />
			</div>
			<div v-loading="loading.show" class="col-span-2">
				<TextEditor
					v-model="data.email_template.body"
					ref="refTextEditor"
					:options="ckeditor"
					@update:modelValue="updateStats"
					with-image-upload>
					<template #toolbar>
						<Button
							class="ml-2"
							@click="handlePreviewRevisedOffer"
							:loading="loading.preview_email"
							color="teal"
							dense>
							<i class="material-icons mr-1">visibility</i>
							Preview Email
						</Button>
						<Button
							@click="openModal('reset_template')"
							:loading="loading.reset_template"
							class="ml-auto"
							color="orange"
							view="smooth"
							dense>
							<i class="material-icons">restart_alt</i>
							Reset to Default
						</Button>
					</template>
				</TextEditor>
				<div class="flex justify-between items-center mt-2">
					<p class="text-blue-03" v-if="!isOverLimit">Revised Offer info can be atmost 500 characters</p>
					<p class="text-red-04" v-else>Character limit exceeded! Maximum is 500 characters.</p>
					<div class="text-gray-600 p-2 rounded bg-gray-100" :class="{'text-red-500': isOverLimit}">
						Paragraphs: {{ stats.paragraphs }}, Words: {{ stats.words }}, Characters (with HTML): {{ stats.chars }}/500
					</div>
				</div>
				<Table class="mt-6" v-if="shortCodes.length > 0" :data="shortCodes">
					<TableColumn label="Code">
						<template #default="scope">
							<div :class="['short-code', scope.row.is_disabled ? 'short-code--disabled' : '']">
								<span class="mr-2">{{ scope.row.code }}</span>
								<i
									v-if="scope.row.is_disabled ? scope.row.disabled : true"
									class="material-symbols-outlined text-lg text-teal-04"
									style="font-variation-settings: 'FILL' 0"
									@click="copyText(scope.row.code)">
									content_copy
								</i>
								<Tooltip v-if="scope.row.tooltip" placement="top">
									<template #content>
										<div class="w-96">
											<span v-html="scope.row.tooltip"></span>
										</div>
									</template>
									<template #default>
										<i class="material-icons text-lg text-teal-04">info</i>
									</template>
								</Tooltip>
							</div>
						</template>
					</TableColumn>
					<TableColumn label="Description">
						<template #default="scope">
							<div
								:class="[
									'inline-block',
									'short-code',
									scope.row.is_disabled ? 'short-code--disabled' : ''
								]">
								{{ scope.row.description }}
							</div>
						</template>
					</TableColumn>
				</Table>
			</div>
			<template #footer>
				<div class="flex justify-end">
					<Button color="teal" @click="updateTemplate" :loading="loading.update">Save</Button>
				</div>
			</template>
		</Card>
	</div>
	<Modal v-model="modal.reset_template" hide-footer hide-header size="xs">
		<template #default>
			<div class="py-10">
				<img class="mb-10 mx-auto" src="@/assets/icon/warning.svg" alt="Warning icon" />
				<div class="mt-8 mb-10">
					<h5 class="mb-4 text-center">Reset Email Template</h5>
					<p class="text-center mb-6">
						Are you sure you want to reset to the default email template?
					</p>
				</div>
				<div class="text-center">
					<Button
						@click="closeModal('reset_template')"
						view="outlined"
						color="teal"
						class="bg-white"
						:disabled="loading.reset_template">
						Cancel
					</Button>
					<Button
						@click="handleResetTemplate"
						view="smooth"
						color="orange"
						class="ml-3"
						:loading="loading.reset_template">
						Yes, reset
					</Button>
				</div>
			</div>
		</template>
	</Modal>
	<RevisedOfferPreview
		v-model:show="isPreviewRevisedOffer"
		:body="previewedRevisedOffer"
		:type="data.type"
		title="Preview Text"
		render-type="direct" />
</template>

<script>
import { computed, defineComponent, reactive, ref, onMounted, watch } from 'vue'
import { useRoute } from 'vue-router'
import { Editor as ClassicEditor } from '@erdeprsty/ckeditor5-custom-build'
import { useRevisedOffer } from 'composable/setting/revisedOffer'
import { useUser } from 'composable/user'

import { ElMessage } from 'element-plus'
import TextEditor from 'components/TextEditor.vue'
import Breadcrumb from 'components/Breadcrumb.vue'
import Card from 'bbUi/components/card'
import Button from 'bbUi/components/button'
import Modal from 'bbUi/components/modal'
import Input from 'bbUi/components/input'
import RevisedOfferPreview from 'components/RevisedOffer/RevisedOfferPreview.vue'

export default defineComponent({
	components: { RevisedOfferPreview, Modal, Button, Breadcrumb, Card, TextEditor, Input },
	async setup() {
		const {
			state,
			data,
			loading,
			modal,
			openModal,
			closeModal,
			actionShow,
			actionPreviewRevisedOffer,
			actionResetTemplate,
			isPreviewRevisedOffer,
			previewedRevisedOffer,
			actionUpdate
		} = useRevisedOffer()
		const { me } = useUser()
		const route = useRoute()
		const refTextEditor = ref(null)
		
		const stats = ref({
			paragraphs: 0,
			words: 0,
			chars: 0
		})
		
		const isOverLimit = ref(false)

		const breadcrumb = ref([
			{
				text: 'Settings',
				to: {
					name: 'setting'
				}
			},
			{
				text: 'Customer Portal',
				to: {
					name: 'settingCustomerPortal'
				}
			},
			{
				text: 'Edit Template Revised Offer'
			}
		])
		const shortCodes = computed(() => {
			return (data?.email_template?.sort_code || []).map(code => ({
				...code,
				description: code.description.replace(/\\/g, ''),
				tooltip: code.tooltip ? code.tooltip.replace(/\\n/g, '<br/>') : null
			}))
		})
		const ckeditor = reactive({
			editor: ClassicEditor,
			config: {
				htmlSupport: {
					allow: [
						{
							styles: true,
							classes: true
						}
					]
				},
				link: {
					decorators: {
						test: {
							mode: 'automatic',
							callback: url => url === '{:verify-link}',
							classes: 'btn btn-blue btn-filled my-4'
						}
					}
				},
				simpleUpload: {
					uploadUrl: `${
						import.meta.env.VITE_BASE_URL_API
					}/v1/pos/settings/email-template/upload-image/${
						route.params.templateId
					}?buyback_company_token=${me.value.buyback_company_token}`
				}
				// Note: remove wordCount property as we'll handle it manually
			}
		})

		const updateStats = (content) => {
			if (!content) {
				stats.value = { paragraphs: 0, words: 0, chars: 0 }
				return
			}

			// 1. Remove all HTML TAG
			const textOnly = content.replace(/<[^>]*>/g, '')

			// 2. Decode entity HTML like &nbsp;, &amp; dsb.
			const decodeHtml = (html) => {
				const textarea = document.createElement('textarea')
				textarea.innerHTML = html
				return textarea.value
			}

			const plainText = decodeHtml(textOnly)

			// 3. Count char from plain text
			stats.value.chars = plainText.length

			// 4. Count Pharaghraph
			const paragraphCount = (content.match(/<p[^>]*>/g) || []).length
			stats.value.paragraphs = paragraphCount || 1

			// 5. Counting Words
			// const words = plainText.trim().split(/\s+/).filter(w => w.length > 0)
			// stats.value.words = words.length
			// 5. Counting Words skip emojis/symbols
			const words = plainText.trim().split(/\s+/).filter(w => /^[\p{L}\p{N}]+$/u.test(w))
			stats.value.words = words.length

			// 6. Limit validation
			isOverLimit.value = stats.value.chars > 500
		}

		const copyText = text => {
			navigator.clipboard.writeText(text).then(() => {
				ElMessage({
					type: 'success',
					message: 'Text copied to clipboard'
				})
			})
		}
		const handlePreviewRevisedOffer = async () => {
			data.type = data.email_template.name

			await actionPreviewRevisedOffer()
		}
		const handleResetTemplate = async () => {
			await actionResetTemplate(async () => {
				closeModal('reset_template')
				await actionShow({
					id: route.params.templateId,
					type: route.params.type
				})
				if (data.email_template && data.email_template.body) {
					updateStats(data.email_template.body)
				}
			})
		}
		const updateTemplate = async () => {
			loading.update = true
			const isReadyEditor = refTextEditor.value.isEditorReady
			if (!isReadyEditor) return
			const isSaved = await refTextEditor.value.forceSave()
			if (!isSaved) return

			if (isOverLimit.value) {
				ElMessage({
					type: 'error',
					message: 'Character limit exceeded! Please reduce the content to maximum 500 characters.'
				})
				loading.update = false
				return
			}

			await actionUpdate({ id: route.params.templateId, type: route.params.type }, () => {
				ElMessage({
					type: 'success',
					message: 'Email template change'
				})
			})
			loading.update = false
		}

		
		onMounted(() => {
			setTimeout(() => {
				if (data.email_template && data.email_template.body) {
					updateStats(data.email_template.body)
				}
			}, 300)
		})

		await actionShow({
			id: route.params.templateId,
			type: route.params.type
		})
		watch(() => data.email_template?.body, (newValue) => {
			if (newValue) {
				updateStats(newValue)
			}
		}, { immediate: true })

		return {
			state,
			data,
			modal,
			ckeditor,
			breadcrumb,
			refTextEditor,
			isPreviewRevisedOffer,
			previewedRevisedOffer,
			shortCodes,
			loading,
			stats,
			isOverLimit,
			openModal,
			closeModal,
			handlePreviewRevisedOffer,
			handleResetTemplate,
			updateTemplate,
			copyText,
			updateStats
		}
	}
})
</script>
